<template>
  <div>
    <CCard class="p-1">
      <CCardBody>
        <h4 style="color:#8c5c7d"><CIcon name="cil-list" size="lg"/> 異常紀錄</h4>
        <hr class="mt-0 mb-4">
        <CRow>
          <CCol md="3" sm="12">
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">設備:</label>
              <div class="col-sm-8">
                <multiselect v-model="selectedEquip" :options="optionsEquip" label="name" selectLabel="選取" deselectLabel="取消" placeholder="請選擇設備"></multiselect>
              </div>
            </div>
          </CCol>
          <CCol md="5" sm="12">
            <div class="form-group row">
              <label class="col-sm-3 col-form-label text-center">時間區間:</label>
              <div class="col-sm-9 pt-1">
                <flat-pickr v-model="startTime" :config="config1"></flat-pickr>
                <strong> ~ </strong>
                <flat-pickr v-model="endTime" :config="config2"></flat-pickr>
              </div>
            </div>
          </CCol>
          <CCol md="2">
            <CButton color="info" class="mr-2 mb-4" @click="queryData">查詢</CButton>
          </CCol>
        </CRow>
        <CRow>
          <CCol>
            <h6 class="float-right">資料筆數: <CBadge color="warning">{{items.length}}</CBadge></h6>
          </CCol>
        </CRow>
        <CDataTable
          :hover="hover"
          :striped="striped"
          :bordered="bordered"
          :small="small"
          :fixed="fixed"
          :items="items"
          :fields="fields"
          :items-per-page="small ? 20 : 50"
          :dark="dark"
          class="text-center"
          border
          pagination
        >
          <template #no-items-view>
            <br>
            <h5>
              無資料
            </h5>
            <br>
          </template>
          <template #time="{item}">
            <td>
              {{formatTime(item.time)}}
            </td>
          </template>
          <template #action="{item}">
            <td>
              <!-- <CButton v-if="role!=='guest'" type="reset" class="mx-1 my-1" color="info" @click="toMainPage(item)"><font-awesome-icon icon="sign-in-alt"/>&nbsp;&nbsp;續包</CButton> -->
              <!-- <CButton v-if="role!=='guest'" type="reset" class="mx-1 my-1" variant="outline" color="info" @click="Finish(item)"><font-awesome-icon icon="clipboard-check"/>&nbsp;&nbsp;設定完工</CButton>
              <CButton v-if="role==='admin'" type="reset" class="mx-1 my-1" color="danger" @click="Delete1(item)"><font-awesome-icon icon="trash-alt"/>&nbsp;&nbsp;刪除</CButton>
              <CButton type="reset" class="mx-1 my-1" style="background:#6D87B0;color:white" @click="openNewPage(item.spec, item.lot_no)"><font-awesome-icon icon="eye"/> 檢視</CButton> -->
            </td>
          </template>
        </CDataTable>
      </CCardBody>
    </CCard>
  </div>
</template>

<script>
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import 'flatpickr/dist/themes/material_blue.css'

export default {
  components: { flatPickr },
  data() {
    return {
      startTime: null,
      endTime: null,
      config1: {
        enableTime: true,
        dateFormat: "Y-m-d H:i",
        time_24hr: true,
        defaultHour: 0,
        defaultMinute: 0 
      }, 
      config2: {
        enableTime: true,
        dateFormat: "Y-m-d H:i",
        time_24hr: true,
        defaultHour: 0,
        defaultMinute: 0 
      },
      role: '',
      optionsEquip: [],
      selectedEquip: null,
      items: [],    
      fields: [
        { key: 'index', label: '項次', _style: 'background:#FFFFF1' },
        { key: 'time', label: '時間', _style: 'background:#FFFFF1' },
        { key: 'pos', label: '點位', _style: 'background:#FFFFF1' },
        { key: 'msg', label: '異常訊息', _style: 'background:#FFFFF1' },
      ],   
      
      hover: true,
      striped: true,
      bordered: true,
      small: false,
      fixed: true,
      dark: false,
    }
  },
  created() {
    this.role = sessionStorage.getItem('ROLE')
    
    this.$http.get('/api/Equipments')
    .then(res => {
      this.optionsEquip = res.data
    })
  },
  mounted() {
    
  },
  methods: {
    formatTime(time) {
      if(!time) return ''

      let str = String(time)
      return str.substr(0,4)+'/'+str.substr(4,2)+'/'+str.substr(6,2)+' '+str.substr(8,2)+':'+str.substr(10,2)+':'+str.substr(12,2) 
    },
    queryData() {
      if(!this.startTime || !this.endTime) {
        alert('請選擇要查詢的時間區間!')
        return
      }
      
      // 2020-02-04 11:00
      let start = this.startTime.substr(0,4)+this.startTime.substr(5,2)+this.startTime.substr(8,2)+this.startTime.substr(11,2)+this.startTime.substr(14,2)+'00'
      let end = this.endTime.substr(0,4)+this.endTime.substr(5,2)+this.endTime.substr(8,2)+this.endTime.substr(11,2)+this.endTime.substr(14,2)+'00'

      this.$http.get(`/api/${this.selectedEquip.type}/${this.selectedEquip.mac}/${start}/${end}`)
      .then(res => {
        this.items = []
        for(let i=0; i<res.data.length; i++) {
          let arrErr = res.data[i].arrError
          arrErr.forEach(element => {
            this.items.push(element)
          })
        }
        // add index
        this.items.forEach((item, index) => {
          item.index = index+1
        })
      })
      .catch(err => console.log(err.response.data.message))
    },
  }
}
</script>

<style lang="scss" scoped>

</style>